import React, { useEffect, useState } from 'react'
import Layout from '../components/layout'
import Content from '../components/content'
// import the API & graphqlOperation helpers as well as the query
import { API, graphqlOperation } from 'aws-amplify'
import { GetBlog, listBlogs } from '../graphql/queries'

import Amplify from 'aws-amplify'
import config from '../aws-exports'
Amplify.configure(config)

const BlogPage = () => {
  const [blog, setBlog] = useState(null)

  useEffect(() => {

    const getBlog = async () => {
      // const data = await API.graphql(graphqlOperation(GetBlog, { id: "450384eb-5133-41dc-95f9-10e65750ba64" }))
      const data = await API.graphql(graphqlOperation(listBlogs))
      console.log('blog successfully fetched', data.data.listBlogs.items[0].name)
      setBlog(data.data.listBlogs.items[0].name)
    }

    getBlog()

  }, [])

  return (
    <Layout>
      <Content title='Blog posts' titleColor="mdb-color">
        {blog && <p>{blog}</p>}
      </Content>
    </Layout>
  )

}

export default BlogPage
